body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.background {
  background: url("./assets/img/topLeft.png") left top / 10rem no-repeat,
    url("./assets/img/bg_topRight@2x.png") right top / 25rem no-repeat,
    url("./assets/img/bottomRight.png") right bottom / 15rem no-repeat;
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: -100;
}

@media (max-width: 480px) {
  .background {
    background: url("./assets/img/topLeft.png") left top / 8rem no-repeat,
      url("./assets/img/bg_topRight@2x.png") right top / 25rem no-repeat,
      url("./assets/img/bottomRight.png") right bottom / 8rem no-repeat;
    width: 100vw;
    height: 100vh;
    position: fixed;
    z-index: -100;
  }
}
